import React, { useState } from "react";
import { RiFolderSettingsFill } from "react-icons/ri";
import { useParams } from "react-router-dom";
import CalendarView from "./CalendarView";
import ApplicationSession from "./ApplicationSession/ApplicationSession";
import AcademicSession from "./AcademicSession/AcademicSession";

enum sessionSettingsTabs {
  applicationSession = "Application Session",
  academicSession = "Academic Session",
  calenderView = "Calendar View",
}

export const Index = () => {
  const { setting_type } = useParams<{ setting_type: string }>();
  const [selectedTab, setSelectedTab] = useState<sessionSettingsTabs | null>(
    setting_type === "calendar"
      ? sessionSettingsTabs.calenderView
      : setting_type === "academic"
      ? sessionSettingsTabs.academicSession
      : sessionSettingsTabs.applicationSession
  );

  return (
    <div className="">
      <div className="mt-3 rounded flex flex-row justify-between items-center">
        <div className="flex flex-row items-center gap-3">
          <RiFolderSettingsFill className="text-6xl text-blue-600" />
          <div className="flex flex-col">
            <span className="text-blue-600 text-2xl font-bold">
              Session settings
            </span>
          </div>
        </div>
      </div>
      <div className="mt-3 mb-6 flex-col">
        <div className="bg-white w-full border border-white rounded">
          <div className="flex flex-row items-center">
            {Object.values(sessionSettingsTabs).map((item, i) => (
              <div
                key={i}
                onClick={() => setSelectedTab(item)}
                className={`animate__animated ${
                  selectedTab === item
                    ? "bg-blue-100 border-blue-600 text-blue-600 animate__zoomIn"
                    : "bg-white border-white text-gray-600"
                } hover:bg-blue-100 px-4 py-2 cursor-pointer font-bold border-b-4 hover:border-blue-600 hover:text-blue-600 rounded-t mt-1 mx-1`}
              >
                {item}
              </div>
            ))}
          </div>
          <div className="=rounded mx-1 mb-1">
            {selectedTab === sessionSettingsTabs.applicationSession && (
              <ApplicationSession
                isCreateDefault={setting_type === "create-session"}
              />
            )}
            {selectedTab === sessionSettingsTabs.academicSession && (
              <AcademicSession
                isCreateDefault={setting_type === "create-session"}
              />
            )}
            {selectedTab === sessionSettingsTabs.calenderView && (
              <CalendarView />
            )}
          </div>
        </div>
      </div>
    </div>
  );
};
